// axios
import axios from 'axios'
import { Message } from 'element-ui';

axios.defaults.withCredentials = true;

/**
 * 
 * @param {Object} options 

  this.API.requestData({
    isMust: true, //  token  true 
    showLoading: false, //  false 
    method: "GET", //  GET 
    baseURL:"http://192.168.10.23:8082/",//+
    subUrl: "api/repassword", // 
    timeout: 3000, //  3s 
    data: { name: "admin", pasword: "123456" }, // 
    success (res) {
      // 
    },
    fail (error) {
      // 
    }
  });

 */
export function requestData (options) {
  return new Promise((resolve, reject) => {

    if (options.showLoading) {
      store.state.loading = true;
    }

    // 
    let headers = options.headers || {
      'Content-Type': 'application/x-www-form-urlencoded',
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Credentials': 'true'
    };

    //  token
    const isMust = (options.isMust == true || options.isMust == false) ? options.isMust : true;
    if (isMust) headers.authToken = localStorage.getItem('authToken');

    // 
    const XHRReq = axios.create({
      headers,
      withCredentials: true,
      crossDomain: true,
      baseURL: options.baseURL || process.env.VUE_APP_LOCALTEST_URL || '/api/',
      timeout: 23000, //  - 3s
    });

    // 
    XHRReq.interceptors.request.use((config) => {
      return config;
    }, (err) => {
      return Promise.reject(err);
    });

    // 
    let params = new URLSearchParams();
    for (let key in (options.data || {})) {
      params.append(key, options.data[key]);
    }

    // 
    XHRReq({
      url: options.subUrl,
      method: options.method || 'GET',
      params: options.sourceData || params,
    }).then(response => {
      if (options.showLoading) {
        store.state.loading = false;
      }
      if (response.code === 501) { // code (token)
        localStorage.removeItem('authToken');
        Message.error(response.data.msg);
        setTimeout(() => {
          window.location.reload();
          // window.__STATICVUE__.$router.replace('/login');
        }, 1000);
      } else if (response.data.code === 200) { //  code
        options.success && options.success(response.data);
        resolve(response);
      } else { // code
        Message.error((response.data && response.data.message) || ("[" + response.data.code + "]"));
      }
    }).catch(error => {
      if (options.showLoading) {
        store.state.loading = false;
      }
      options.fail && options.fail(error);
      reject(error);
    });
  });
}

export default {
  requestData
}
